import axios from 'axios';
import { postContactUrl, postJobRequestUrl, fileUploadUrl, resumeUploadUrl} from "./API_Endpoints"

let postConfig = {
    headers: {
        'Content-Type': "application/json"
    }
}

let fileConfig = {
    headers: {
        'Content-Type': 'multipart/form-data'
    }
}

function postContactToService(payload) {
    return axios.post(postContactUrl, payload, postConfig);
}

function postJobRequestToService(payload) {
    return axios.post(postJobRequestUrl, payload, postConfig);
}

function fileUpload(file) {

    var data = new FormData();
    data.append('filetoupload', file, file.name);
    return axios.post(fileUploadUrl, data, fileConfig)
}

function resumeUpload(resume) {

    var data = new FormData();
    data.append('resumetoupload', resume, resume.name);
    return axios.post(resumeUploadUrl, data, fileConfig)
}

export { postContactToService, postJobRequestToService , fileUpload, resumeUpload} 	