import React from "react";
import PropTypes from "prop-types";

export default function InfoAreaIcon({ image, color, title, description }) {
  let newDescription = [];
  if (!Array.isArray(description)) {
    newDescription = [description];
  } else {
    newDescription = description;
  }
  return (
    <>
      <div className="text-center py-6">
        <div>
          <img
            alt="..."
            src={image}
            className="mr-10 max-w-60-px"
          />
        </div>
        <h4 className="text-sm text-center tight mt-0 mb-2">{title}</h4>
        <div className="mb-4">
          {newDescription.map((prop, key) => {
            return (
              <p className="text-blueGray-500" key={key}>
                {prop}
              </p>
            );
          })}
        </div>
      </div>
    </>
  );
}

InfoAreaIcon.defaultProps = {
  color: "blueGray",
};
InfoAreaIcon.propTypes = {
  icon: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  color: PropTypes.oneOf([
    "light",
    "dark",
    "blueGray",
    "red",
    "orange",
    "amber",
    "emerald",
    "teal",
    "lightBlue",
    "indigo",
    "purple",
    "pink",
  ]),
};
