import React from "react";
import PropTypes from "prop-types";

// components
import HeadingBadge from "../Components/Heading/HeadingBadge.js";
import CardContactUsAddress from "../Components/Cards/CardContactUsAddress.js";
import InfoAreaIconTitle from "../Components/InfoAreas/InfoAreaIconTitle.js";

export default function ServiceContact({
  headingBadge,
  image,
  infoAreas,
  card,
}) {
  return (
    <>
      <div
        className="bg-blueGray-900 py-20"
        style={{ backgroundImage: "url('" + image + "')" }}
      >
        <div className="container mx-auto px-4">
          <div className="flex flex-wrap -mx-3">
            <div className="mx-auto flex justify-center flex-col text-white px-2 relative lg:w-4/12 w-full md:w-5/12">
              {headingBadge && <HeadingBadge {...headingBadge} />}
              {infoAreas.map((prop, key) => (
                <InfoAreaIconTitle key={key} {...prop} />
              ))}
            </div>
            <div className="mx-auto px-2 relative w-full lg:w-5/12 w-full md:w-7/12">
              <CardContactUsAddress {...card} />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

ServiceContact.defaultProps = {
  card: {},
  infoAreas: [],
  heading2badge: null,
};
ServiceContact.propTypes = {
  // prop to pass to the Heading2Badge component
  heading2badge: PropTypes.object,
  image: PropTypes.string,
  // array of props for the InfoAreaIconTitle component
  infoAreas: PropTypes.arrayOf(PropTypes.object),
  // array of props for the CardContactUsAddress component
  card: PropTypes.object,
};
